function transOption(option) {
    if(!option)return {};
    var newopt={};
    for(var i in option){
        switch (i){
            case 'viewmode':
                newopt['viewMode']=option[i];
                break;
            case 'keepopen':
                newopt['keepOpen']=option[i];
                break;
            default:
                newopt[i]=option[i];
        }
    }
    return newopt;
}

//
if($.fn.datetimepicker) {
    var tooltips= {
        today: '',
        clear: '',
        close: '',
        selectMonth: '',
        prevMonth: '',
        nextMonth: '',
        selectYear: '',
        prevYear: '',
        nextYear: '',
        selectDecade: '',
        selectTime:'',
        prevDecade: '',
        nextDecade: '',
        prevCentury: '',
        nextCentury: ''
    };


    $('.datepicker').each(function(){
        var config=$.extend({
            tooltips:tooltips,
            format: 'YYYY-MM-DD',
            locale: 'zh-cn',
            showClear:true,
            showTodayButton:true,
            showClose:true,
            keepInvalid:true
        },transOption($(this).data()));

        $(this).datetimepicker(config);
    });

    $('.date-range').each(function () {
        var from = $(this).find('[name=fromdate],.fromdate'), to = $(this).find('[name=todate],.todate');
        var options = $.extend({
            tooltips:tooltips,
            format: 'YYYY-MM-DD',
            locale:'zh-cn',
            showClear:true,
            showTodayButton:true,
            showClose:true,
            keepInvalid:true
        },$(this).data());
        from.datetimepicker(options).on('dp.change', function () {
            if (from.val()) {
                to.data('DateTimePicker').minDate(from.val());
            }
        });
        to.datetimepicker(options).on('dp.change', function () {
            if (to.val()) {
                from.data('DateTimePicker').maxDate(to.val());
            }
        });
    });
}